import React, { Component } from 'react'
import axios from 'axios';
import {withAuth} from './../lib/Auth'

class SaveLoadMenu extends Component {
    state = {
      saveSlotNumber: 1
    }

    //this is to check if there is something in the slot, and to change its color accordingly
    isSlotSaved = () => {
      let slotsToRender = [];
      for (let i = 1; i < 4; i++) {
        let slotStyle = {};
        if (this.state.slotNumber === i) {
          slotStyle["background-color"] = "red";
        } else if (!JSON.parse(localStorage.getItem(i))) {
          slotStyle["background-color"] = "green";
        }
        slotsToRender.push(
          <button className="save-load-btn" onClick={() => this.setState({ slotNumber: i })} style={slotStyle}>
            Save file {i}
          </button>
        );
      }
      return <div className="save-load-buttons">{slotsToRender}</div>;
    }

    //
    renderChoiceMenu = () => {
      return (
        <div className="overlay-choices overlay-choices-slot">
          {JSON.parse(localStorage.getItem(this.state.saveSlotNumber)).choiceOptions.map(key => (
            <button className="choice-button">{key.content}</button>
          ))}
        </div>
      );
    }

    //


  render() {
    return (
        <div className="overlay overlay-save-load">
        <ul className="header">
          <li>
            <a>{this.props.menuType}</a>
          </li>
          <li className="exit-button" onClick={this.props.toggleMenu}>
          </li>
        </ul>
        {this.isSlotSaved()}
      </div>
    )
  }
}

export default SaveLoadMenu;